
import { isMobile } from 'react-device-detect';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core';
import Container from '../components/container';
import NavigationBar from '../components/navigation';


export default function Home() {
	const styles = useStyles();

	return (
		<Container>
			<div style={{ marginTop: 250 }}>
				<Paper className={styles.descJor}>
					" Soy Jorgelina, nac√≠ en Santa Fe. Soy Vicepresidenta Segunda del
					Concejo Deliberante de Nuestra Ciudad. Mi compromiso es trabajar todos
					los d√≠as para contribuir a que seamos una ciudad pujante, inclusiva y
					con igualdad de oportunidades para todos los santafesinos y
					santafesinas "
				</Paper>
			</div>

			<div>
				<NavigationBar />
			</div>
		</Container>
	);
}

const useStyles = makeStyles(() => ({
	descJor: {
		width: isMobile ? '95%' : '38%',
		padding: isMobile ? 20 : 35,
		lineHeight: isMobile ? 1.4 : 1.6,
		letterSpacing: isMobile ? 1.5 : 2.2,
		fontSize: isMobile ? 14 : 18,
		borderRadius: 10,
		marginLeft: isMobile ? 'auto' : '8%',
		marginRight: isMobile ? 'auto' : null,
		/* 	textAlign: 'justify', */
		backgroundColor: 'rgba(228, 235, 241,0.7)',
	},
}));
